.product-container {
  max-width: 1400px;
  margin: 0 auto;
  height: fit-content;
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  padding: 30px 10px;
  gap: 25px;
  color: var(--text-primary);
}

.product-details {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 30px;
}

.product-info {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.product-relative {
  width: 50%;
}

.product-description {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.product-description--extra {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.product-description--extra__share {
  display: flex;
  align-items: center;
  gap: 10px;
}

.share-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: var(--text-primary);
  transition: color 0.3s ease;
}

.share-button:hover {
  color: var(--btn-secondary-hover);
}

.product-description--extra__report a {
  font-weight: 500;
  color: var(--error);
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.product-description--extra__report a:hover {
  color: var(--btn-fav-hover);
}

.product-description--extra__report a:active {
  color: var(--btn-fav-active);
}

.product-description--value {
  max-width: 80%;
  font-weight: 400;
}

.product-description--details {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.product-description--details__header {
  color: var(--text-secondary);
}

.product-details__gallery {
  display: flex;
  height: 50%;
  gap: 15px;
  justify-content: center;
}

.product-details__thumbnails {
  display: flex;
  gap: 3px;
  flex-direction: column;
}

.product-details__thumbnail {
  padding: 5px;
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.product-details__main-image {
  padding: 10px;
  max-width: 60%;
  height: auto;
  border-radius: 20px;
  object-fit: cover;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.product-details__main-image:hover,
.product-details__thumbnail:hover {
  background-color: var(--border-color);
}

.product-details__info {
  display: flex;
  flex-direction: column;
  width: 40%;
  gap: 25px;
}

.product-details__title {
  font-size: 1.8rem;
  max-width: 350px;
}

.product-details__price {
  font-size: 1.8rem;
  font-weight: 700;
}

.product-details__size {
  width: fit-content;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid var(--border-color);
}

.product-details__actions {
  display: flex;
  gap: 15px;
}

.product-details__button {
  font-size: 1.2rem;
  color: var(--btn-primary-text);
  background-color: var(--btn-primary-bg);
  padding: 15px;
  width: 200px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-details__button--cart:hover {
  background-color: var(--text-secondary);
}

