  color: var(--btn-primary-text);
  border: 1px solid var(--border-color);
  background-color: var(--btn-secondary-bg);
}

.product-details__button--favourite:hover {
  color: var(--btn-primary-bg);
  background-color: var(--btn-secondary-text);
} */

.product-details__seller {
  height: fit-content;
  width: fit-content;
  padding: 3%;
  border: 1px solid var(--border-color);
}

.product-details__seller-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.seller-info__row {
  display: flex;
  align-items: center;
  gap: 10px;
}

@media (max-width: 958px) {
  .product-details {
    padding: 0;
    gap: 20px;
    flex-direction: column;
  }
  .product-details__info {
    width: 100%;
  }

  .product-details__gallery {
    align-items: center;
    flex-direction: column;
  }
  .product-details__thumbnails {
    flex-direction: row;
    order: 2;
  }
}

@media (max-width: 478px) {
  .product-details__title {
    font-size: 1.2rem;
  }
  .product-details__actions {
    padding: 0 5px;
  }
  .product-details__button {
    width: 100%;
  }

  .product-details__seller {
    text-align: center;
  }
  .product-description--value {
    max-width: 100%;
  }
  .report-link {
    font-size: 0.9rem;
  }
}
